import _ from "lodash";

// 
const uniqueArray = () => {
  let objects = [
    { x: 1, y: 2 },
    { x: 2, y: 1 },
    { x: 1, y: 2 },
  ];
  let result = _.uniqWith(objects, _.isEqual);
  console.log(result);
};

const findIndex = () => {
  let testArr = [
    { name: "", age: 16 },
    { name: "", age: 17 },
    { name: "", age: 26 },
  ];
  let index = _.findIndex(testArr, { name: "" }); // => 1
  console.log(index);
};
export { uniqueArray, findIndex };
