import React from "react";
import { useHistory } from "react-router-dom";
import { Button } from "antd";

const HookRouter = (props) => {
  const history = useHistory();

  const toPage = (path) => {
    history.push({
      pathname: `${path}:adv`,
      search: "?resume=99",
      state: { name: "kafuka" },
      others: { id: 990 },
    });
  };

  const toPage2 = () => {
    const level = "mid";
    const name = "fgm";
    const id = 990;
    const other = 100;
    /* 
    paramsname/id1.url 2. 
    searchlevelurlhooks
    stateid/other, 
     */
    history.push(
      `/hookRoute/${name}/${id}?level=${level}`,
      // `/hookRoute/${name}?level=${level}`,
      { id },
      { other } // 
    );
  };

  return (
    <div>
      <Button onClick={() => toPage("/hookRoute")}>1:hook</Button>
      <Button onClick={() => toPage2()}>2:hook</Button>
    </div>
  );
};

export default HookRouter;
