import React, { Component } from "react";
import { Button, Select } from "antd";
import { uniqueArray } from "@common/utils/lodashPlus";
import _ from "lodash";
import { lodashList } from "./constants";

const { Option, OptGroup } = Select;
const funs = {
  uniqueArray: uniqueArray,
};

class ClassRoute extends Component {
  constructor(props) {
    super(props);
    this.state = {
      curFun: null,
      objArr: [{ sid: 123 }, { sid: 456 }, { sid: 789 }],
    };
  }

  onSelect = (LabeledValue, option) => {
    this.setState({
      curFun: this[option.label] || funs[option.label],
    });
  };

  filterOption = (input, option) => {
    return option?.children?.toLowerCase().indexOf(input.toLowerCase()) >= 0;
  };

  isNumberFun = () => {
    // :  value Number  
    // :  Infinity, -Infinity,  NaN _.isFinite 
    _.isNumber(3);
    // => true
    _.isNumber(Number.MIN_VALUE);
    // => true
    _.isNumber(Infinity);
    // => true
    _.isNumber("3");
    // => false
    console.log(_.isNumber(3));
  };

  isObjectFun = () => {
    _.isObject({});
    // => true

    _.isObject([1, 2, 3]);
    // => true

    _.isObject(_.noop);
    // => true 

    _.isObject(null);
    // => false
  };

  // 
  cloneDeepFun = () => {
    var objects = [{ a: 1 }, { b: 2 }];

    var deep = _.cloneDeep(objects);
    console.log(deep[0] === objects[0]);
    // => false
  };

  //  Function n 
  timesFun = () => {
    _.times(3, String);
    // => ['0', '1', '2']

    const t2 = _.times(4, _.constant(0));
    // => [0, 0, 0, 0]

    const t3 = _.times(3, (id) => {
      console.log(id);
    });

    console.log(t2, t3, 90);
  };

  sumByFun = () => {
    var objects = [{ n: 4 }, { n: 2 }, { n: 8 }, { n: 6 }];

    _.sumBy(objects, function (o) {
      return o.n;
    });
    // => 20

    // The `_.property` iteratee shorthand.
    _.sumBy(objects, "n");
    // => 20

    const ob = _.sumBy(objects, function (o) {
      return o.n * 2;
    });
    console.log(ob, 99);
  };

  uniqByFun = () => {
    _.uniqBy([2.1, 1.2, 2.3], Math.floor);
    // => [2.1, 1.2]

    // The `_.property` iteratee shorthand.
    _.uniqBy([{ x: 1 }, { x: 2 }, { x: 1 }], "x");
    // => [{ 'x': 1 }, { 'x': 2 }]
  };

  // array size  array 
  chunkFun = () => {
    _.chunk(["a", "b", "c", "d"], 2);
    // => [['a', 'b'], ['c', 'd']]

    _.chunk(["a", "b", "c", "d"], 3);
    // => [['a', 'b', 'c'], ['d']]
  };

  // string
  capitalizeFun = () => {
    _.capitalize("FRED");
    // => 'Fred'

    console.log(_.capitalize("product_listings"));
    // => 'Product_listings'

    console.log(_.capitalize("-product-listings-"));
    // => '-product-listings-'
  };

  //  Fisher-Yates shuffle 
  shuffleFun = () => {
    _.shuffle([1, 2, 3, 4]);
    // => [4, 1, 3, 2]
  };

  // array
  differenceFun = () => {
    const { objArr } = this.state;
    _.difference([3, 2, 1], [4, 2]);
    // => [3, 1]

    // 
    const newArr = _.difference(objArr, [{ sid: 123 }]);
    console.log(newArr, "--newArr");
  };

  // _.difference  comparator arrayvalues 
  differenceWithFun = () => {
    const { objArr } = this.state;
    var objects = [
      { x: 1, y: 2 },
      { x: 2, y: 1 },
    ];

    _.differenceWith(objects, [{ x: 1, y: 2 }], _.isEqual);
    // => [{ 'x': 2, 'y': 1 }]

    const d1 = _.differenceWith(
      objArr,
      [{ sid: 123 }, { sid: 456 }],
      _.isEqual
    );
    console.log(d1);
    // => [{ sid: 789 }]
  };

  //  array 
  headFun = () => {
    _.head([1, 2, 3]);
    // => 1

    _.head([]);
    // => undefined

    let h1 = _.head();
    let h2 = _.head("");
    let h3 = _.head(undefined);
    console.log(h1, h2, h3); // => undefined
  };

  // array
  lastFun = () => {
    _.last([1, 2, 3]);
    // => 3

    _.last([]);
    // => undefined
  };

  //  object 
  pickFun = () => {
    var object = { a: 1, b: "2", c: 3 };

    const p1 = _.pick(object, ["a", "c"]);
    // => { 'a': 1, 'c': 3 }

    const p2 = _.pick(object, "c");
    // => {'c': 3 }
    console.log(p1, p2);
  };

  //  object  predicate 
  pickByFun = () => {
    var object = { a: 1, b: "2", c: 3 };

    _.pickBy(object, _.isNumber);
    // => { 'a': 1, 'c': 3 }
  };

  // _.pick
  omitFun = () => {
    var object = { a: 1, b: "2", c: 3 };

    _.omit(object, ["a", "c"]);
    // => { 'b': '2' }
  };

  // string
  lowerCaseFun = () => {
    _.lowerCase("--Foo-Bar--");
    // => 'foo bar'

    _.lowerCase("fooBar");
    // => 'foo bar'

    _.lowerCase("__FOO_BAR__");
    // => 'foo bar'

    const lowerCase = _.lowerCase("weweeeDDDDwwwIIUD");
    const lowerCase2 = _.lowerCase("Dec1215 Design");
    const lowerCase3 = _.lowerCase("121Dec1215 Design2323");
    const lowerCase4 = _.lowerCase("121 Dec1215 Design 2323");
    const lowerCase5 = _.lowerCase("AAA 121 Dec1215 De/sign 232, 3 B?BB");

    console.log(lowerCase);
    console.log(lowerCase2);
    console.log(lowerCase3);
    console.log(lowerCase4);
    console.log(lowerCase5);

    console.log("weweeeDDDDwwwIIUD".toLowerCase());
    console.log("Dec1215 Design".toLowerCase());
    console.log("121Dec1215 Design2323".toLowerCase());
    console.log("121 Dec1215 Design 2323".toLowerCase());
    console.log("AAA 121 Dec1215 De/sign 232, 3 B?BB".toLowerCase());
  };

  //  value set length  0  arguments object, array, string jquery
  // 
  isEmptyFun = () => {
    _.isEmpty({});
    console.log(_.isEmpty({}), _.isEmpty(null));
    // => true

    _.isEmpty(null);
    // => true

    _.isEmpty(true);
    // => true

    _.isEmpty(1);
    // => true

    _.isEmpty([1, 2, 3]);
    // => false

    _.isEmpty({ a: 1 });
    // => false
  };

  //  
  isEqualFun = () => {
    var object = { a: 1 };
    var other = { a: 1 };

    _.isEqual(object, other);
    // => true

    console.log(object === other);
    // => false
  };

  // SameValueZero
  intersectionFun = () => {
    _.intersection([2, 1], [4, 2], [1, 2]);
    // => [2]

    // 
    const i1 = _.intersection(
      [{ sid: 123 }, { sid: 456 }, { sid: 789 }],
      [{ sid: 789 }],
      [{ sid: 789 }]
    );
    console.log(i1);
  };

  // _.intersection iteratee arrays
  intersectionByFun = () => {
    _.intersectionBy([2.1, 1.2], [4.3, 2.4], Math.floor);
    // => [2.1]

    // The `_.property` iteratee shorthand.
    _.intersectionBy([{ x: 1 }], [{ x: 2 }, { x: 1 }], "x");
    // => [{ 'x': 1 }]
  };

  groupByFun = () => {
    _.groupBy([6.1, 4.2, 6.3], Math.floor);
    // => { '4': [4.2], '6': [6.1, 6.3] }

    // The `_.property` iteratee shorthand.
    _.groupBy(["one", "two", "three"], "length");
    // => { '3': ['one', 'two'], '5': ['three'] }
  };

  //  string start case.
  startCaseFun = () => {
    _.startCase("--foo-bar--");
    // => 'Foo Bar'

    _.startCase("fooBar");
    // => 'Foo Bar'

    _.startCase("__FOO_BAR__");
    // => 'FOO BAR'
  };

  // string
  camelCaseFun = () => {
    _.camelCase("Foo Bar");
    // => 'fooBar'

    _.camelCase("--foo-bar--");
    // => 'fooBar'

    _.camelCase("__FOO_BAR__");
    // => 'fooBar'
  };

  // 
  unionFun = () => {
    _.union([2], [1, 2]);
    // => [2, 1]

    const arrs = {
      arr1: [1, 2, 5, 7],
      arr2: [2, 5, 7],
      arr3: [1, 3, 5, 7],
      arr4: [10, 2, 5, 8, 10, 9],
      arr5: [23, 2, 5, 17],
    };
    const u1 = _.union(...arrs);
    console.log(u1);
  };

  //  object 
  // Note: 
  keysFun = () => {
    function Foo() {
      this.a = 1;
      this.b = 2;
    }

    Foo.prototype.c = 3;

    _.keys(new Foo());
    // => ['a', 'b'] (iteration order is not guaranteed)

    _.keys("hi");
    // => ['0', '1']

    let ob = { name: "kevin", age: 20 };
    let k1 = _.keys(ob);
    console.log(k1);
    // => ["name", "age"]
  };

  fillFun = () => {
    var array = [1, 2, 3];

    _.fill(array, "a");
    console.log(array);
    // => ['a', 'a', 'a']

    _.fill(Array(3), 2);
    // => [2, 2, 2]

    _.fill([4, 6, 8, 10], "*", 1, 3);
    // => [4, '*', '*', 10]

    // fill
    const arrayWithObject = _.fill(Array(3), { name: "" });
    arrayWithObject[0].name = "new";
    console.log(arrayWithObject);
  };

  filterFun = () => {
    var users = [
      { user: "barney", age: 36, active: true },
      { user: "fred", age: 40, active: false },
    ];

    _.filter(users, function (o) {
      return !o.active;
    });
    // => objects for ['fred']

    // The `_.matches` iteratee shorthand.
    _.filter(users, { age: 36, active: true });
    // => objects for ['barney']

    // The `_.matchesProperty` iteratee shorthand.
    _.filter(users, ["active", false]);
    // => objects for ['fred']

    // The `_.property` iteratee shorthand.
    _.filter(users, "active");
    // => objects for ['barney']
  };

  // object  object 
  invertFun = () => {
    var object = { a: 1, b: 2, c: 1 };

    _.invert(object);
    // => { '1': 'c', '2': 'b' }
  };

  //  value 
  isIntegerFun = () => {
    _.isInteger(3);
    // => true

    _.isInteger(Number.MIN_VALUE);
    // => false

    _.isInteger(Infinity);
    // => false

    _.isInteger("3");
    // => false
  };

  mapValuesFun = () => {
    var users = {
      fred: { user: "fred", age: 40 },
      pebbles: { user: "pebbles", age: 1 },
    };

    _.mapValues(users, function (o) {
      return o.age;
    });
    // => { 'fred': 40, 'pebbles': 1 } (iteration order is not guaranteed)

    // The `_.property` iteratee shorthand.
    _.mapValues(users, "age");
    // => { 'fred': 40, 'pebbles': 1 } (iteration order is not guaranteed)
  };

  keyByFun = () => {
    var array = [
      { dir: "left", code: 97 },
      { dir: "right", code: 100 },
    ];

    _.keyBy(array, function (o) {
      return String.fromCharCode(o.code);
    });
    // => { 'a': { 'dir': 'left', 'code': 97 }, 'd': { 'dir': 'right', 'code': 100 } }

    _.keyBy(array, "dir");
    // => { 'left': { 'dir': 'left', 'code': 97 }, 'right': { 'dir': 'right', 'code': 100 } }

    let array1 = [
      { dir: "left", code: 97 },
      { dir: "right", code: 100 },
      { dir: "left", code: 88 },
    ];
    let k1 = _.keyBy(array1, "dir");
    console.log(k1);
  };

  assignFun = () => {
    function Foo() {
      this.a = 1;
    }

    function Bar() {
      this.c = 3;
    }

    Foo.prototype.b = 2;
    Bar.prototype.d = 4;

    _.assign({ a: 0 }, new Foo(), new Bar());
    // => { 'a': 1, 'c': 3 }
  };

  // mergeassignmerge:+
  mergeFun = () => {
    var object = {
      a: [{ b: 2 }, { d: 4 }],
    };

    var other = {
      a: [{ c: 3 }, { e: 5 }],
    };

    _.merge(object, other);
    // => { 'a': [{ 'b': 2, 'c': 3 }, { 'd': 4, 'e': 5 }] }

    let o1 = { a: 1 };
    let o2 = { b: { c: 2, d: 3 } };
    let o3 = { b: { e: 4, d: 5 } };

    let m1 = _.merge(o1, o2, o3);
    console.log(m1);
  };
  // string target 
  startsWithFun = () => {
    _.startsWith("abc", "a");
    // => true

    _.startsWith("abc", "b");
    // => false

    _.startsWith("abc", "b", 1);
    // => true
  };
  // 
  identityFun = () => {
    var object = { a: 1 };

    console.log(_.identity(object) === object);
    // => true

    console.log(_.identity((1, 2, 5)));
  };
  //  value  null  undefined
  isNilFun = () => {
    _.isNil(null);
    // => true

    _.isNil(void 0);
    // => true

    _.isNil(NaN);
    // => false
  };
  toNumberFun = () => {
    _.toNumber(3.2);
    // => 3.2

    _.toNumber(Number.MIN_VALUE);
    // => 5e-324

    _.toNumber(Infinity);
    // => Infinity

    _.toNumber("3.2");
    // => 3.2
  };
  // _.find   predicate  key
  findKeyFun = () => {
    var users = {
      barney: { age: 36, active: true },
      fred: { age: 40, active: false },
      pebbles: { age: 1, active: true },
    };

    _.findKey(users, function (o) {
      return o.age < 40;
    });
    // => 'barney' (iteration order is not guaranteed)

    // The `_.matches` iteratee shorthand.
    _.findKey(users, { age: 1, active: true });
    // => 'pebbles'

    // The `_.matchesProperty` iteratee shorthand.
    _.findKey(users, ["active", false]);
    // => 'fred'

    // The `_.property` iteratee shorthand.
    _.findKey(users, "active");
    // => 'barney'
  };
  //  value  Array 
  isArrayFun = () => {
    _.isArray([1, 2, 3]);
    // => true

    _.isArray(document.body.children);
    // => false

    _.isArray("abc");
    // => false

    _.isArray(_.noop);
    // => false
  };
  isStringFun = () => {
    _.isArray([1, 2, 3]);
    // => true

    _.isArray(document.body.children);
    // => false

    _.isArray("abc");
    // => false

    _.isArray(_.noop);
    // => false
  };
  // arraynn1
  dropFun = () => {
    _.drop([1, 2, 3]);
    // => [2, 3]

    _.drop([1, 2, 3], 2);
    // => [3]

    _.drop([1, 2, 3], 5);
    // => []

    _.drop([1, 2, 3], 0);
    // => [1, 2, 3]
  };
  // arraynn1
  dropRightFun = () => {
    _.dropRight([1, 2, 3]);
    // => [1, 2]

    _.dropRight([1, 2, 3], 2);
    // => [1]

    _.dropRight([1, 2, 3], 5);
    // => []

    _.dropRight([1, 2, 3], 0);
    // => [1, 2, 3]
  };
  // SameValueZero
  // : _.pull, 
  withoutFun = () => {
    _.without([2, 1, 2, 3], 1, 2);
    // => [3]

    // 
    let a1 = [{ id: 11 }, { id: 22 }, { id: 33 }];
    let w1 = _.without(a1, { id: 11 });
    console.log(w1);
  };
  // stringsnake case.
  snakeCaseFun = () => {
    _.snakeCase("Foo Bar");
    // => 'foo_bar'

    _.snakeCase("fooBar");
    // => 'foo_bar'

    _.snakeCase("--FOO-BAR--");
    // => 'foo_bar'
  };
  //  objectpath  value  undefined  defaultValue 
  getFun = () => {
    var object = { a: [{ b: { c: 3 } }] };

    _.get(object, "a[0].b.c");
    // => 3

    _.get(object, ["a", "0", "b", "c"]);
    // => 3

    _.get(object, "a.b.c", "default");
    // => 'default'
  };

  randomFun = () => {
    _.random(0, 5);
    // => an integer between 0 and 5

    _.random(5);
    // => also an integer between 0 and 5

    _.random(5, true);
    // => a floating-point number between 0 and 5

    _.random(1.2, 5.2);
    // => a floating-point number between 1.2 and 5.2
  };

  upperFirstFun = () => {
    _.upperFirst("fred");
    // => 'Fred'

    _.upperFirst("FRED");
    // => 'FRED'

    console.log(_.upperFirst("product_listings"));
    // => 'Product_listings'

    console.log(_.upperFirst("-product-listings-"));
    // => '-product-listings-'
  };

  render() {
    const { curFun } = this.state;
    return (
      <div>
        <Select
          showSearch
          style={{ width: 200 }}
          placeholder="Select a lodash function"
          optionFilterProp="label"
          filterOption={this.filterOption}
          onSelect={this.onSelect}
        >
          {lodashList.map((g) => (
            <OptGroup label={g.type} key={g.type}>
              {g.funs.map((i) => (
                <Option value={i.value} label={i.cb} key={i.value}>
                  {i.value}
                </Option>
              ))}
            </OptGroup>
          ))}
        </Select>
        <Button onClick={curFun}>lodashFun</Button>
        {_.times(3, (id) => (
          <div key={id}>{id}</div>
        ))}
      </div>
    );
  }
}

export default ClassRoute;
