import React from "react";
import { Select, Spin } from "antd";

import debounce from "lodash/debounce";
import { sites } from "@common/mockArray";

import styles from "./index.less";

const { Option } = Select;
class AntSelect extends React.Component {
  constructor(props) {
    super(props);
    this.lastFetchId = 0;
    this.fetchUser = debounce(this.fetchUser, 800);
  }

  state = {
    data: [],
    value: [],
    fetching: false,
  };

  componentDidMount() {
    // document.getElementById('select').onmousedown = (e) => {
    //   if(e && e.preventDefault) {
    //     e.preventDefault();
    //   }
    // }
    this.autocomplete(document.getElementById("myInput"), sites);
  }

  /*input */
  autocomplete = (inp, arr) => {
    let currentFocus;
    /*  input  input */
    inp.addEventListener("input", function (e) {
      let a,
        b,
        i,
        val = this.value;
      /**/
      closeAllLists();
      if (!val) {
        return false;
      }
      currentFocus = -1;
      /* DIV */
      a = document.createElement("DIV");
      a.setAttribute("id", this.id + "autocomplete-list");
      a.setAttribute("class", "autocomplete-items");
      // element.className = 'xxxClass';
      /*DIV */
      this.parentNode.appendChild(a);
      /**/
      for (i = 0; i < arr.length; i++) {
        /**/
        if (arr[i].substr(0, val.length).toUpperCase() === val.toUpperCase()) {
          /* DIV  */
          b = document.createElement("DIV");
          /**/
          b.innerHTML = "<strong>" + arr[i].substr(0, val.length) + "</strong>";
          b.innerHTML += arr[i].substr(val.length);
          /* input */
          b.innerHTML += "<input type='hidden' value='" + arr[i] + "'>";
          /*DIV */
          b.addEventListener("click", function (e) {
            /* input */
            inp.value = this.getElementsByTagName("input")[0].value;
            /**/
            closeAllLists();
          });
          a.appendChild(b);
        }
      }
    });
    /**/
    inp.addEventListener("keydown", function (e) {
      var x = document.getElementById(this.id + "autocomplete-list");
      if (x) x = x.getElementsByTagName("div");
      if (e.keyCode === 40) {
        /*currentFocus  1*/
        currentFocus++;
        /**/
        addActive(x);
      } else if (e.keyCode === 38) {
        //up
        /**/
        currentFocus--;
        /**/
        addActive(x);
      } else if (e.keyCode === 13) {
        /* ENTER  submit */
        e.preventDefault();
        if (currentFocus > -1) {
          /**/
          if (x) x[currentFocus].click();
        }
      }
    });
    function addActive(x) {
      /**/
      if (!x) return false;
      /**/
      removeActive(x);
      if (currentFocus >= x.length) currentFocus = 0;
      if (currentFocus < 0) currentFocus = x.length - 1;
      /* "autocomplete-active" */
      x[currentFocus].classList.add("autocomplete-active");
    }
    function removeActive(x) {
      /* "autocomplete-active" */
      for (var i = 0; i < x.length; i++) {
        x[i].classList.remove("autocomplete-active");
      }
    }
    function closeAllLists(elmnt) {
      /**/
      var x = document.getElementsByClassName("autocomplete-items");
      for (var i = 0; i < x.length; i++) {
        if (elmnt !== x[i] && elmnt !== inp) {
          x[i].parentNode.removeChild(x[i]);
        }
      }
    }
  };

  fetchUser = (value) => {
    console.log("fetching user", value);
    this.lastFetchId += 1;
    const fetchId = this.lastFetchId;
    this.setState({ data: [], fetching: true });
    fetch("https://randomuser.me/api/?results=5")
      .then((response) => response.json())
      .then((body) => {
        if (fetchId !== this.lastFetchId) {
          // for fetch callback order
          return;
        }
        const data = body.results.map((user) => ({
          text: `${user.name.first} ${user.name.last}`,
          value: user.login.username,
        }));
        this.setState({ data, fetching: false });
      });
  };

  handleChange = (value) => {
    this.setState({
      value,
      data: [],
      fetching: false,
    });
  };

  preview = () => {
    console.log("");
  };

  render() {
    const { fetching, data, value } = this.state;
    return (
      <div className={styles.selectWrap}>
        <Select
          mode="multiple"
          labelInValue
          value={value}
          placeholder="Select users"
          notFoundContent={fetching ? <Spin size="small" /> : null}
          filterOption={false}
          onSearch={this.fetchUser}
          onChange={this.handleChange}
          style={{ width: "200px" }}
        >
          {data.map((d) => (
            <Option
              key={d.value}
            >{`${d.text}     ''    ${d.value}`}</Option>
          ))}
        </Select>
        {/* autocomplete="off"  */}
        <form
          autoComplete="off"
          action="/index.php"
          className={styles.formWrap}
        >
          <div className={styles.autoComplete} style={{ width: "300px" }}>
            <input
              id="myInput"
              type="text"
              name="s"
              placeholder=""
            />
          </div>
          <input type="submit" />
        </form>
      </div>
    );
  }
}
export default AntSelect;
