import React, { Component } from "react";
import ChildA from "./components/ChildA";
import ChildB from "./components/ChildB";
import { Button } from "antd";
import styles from "./index.less";

class MultiComs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      picData: {
        a: {
          a1: 0,
        },
        b: {
          b1: 0,
        },
      },
    };
  }

  // picData.a.a1, ChildBAGrandSonBPureComponent
  changeState = () => {
    this.setState(({ picData }) => ({
      picData: {
        ...picData,
        a: {
          a1: picData.a.a1 + 1,
        },
      },
    }));
  };

  // b,ChildB
  changeState2 = () => {
    const { picData } = this.state;
    this.setState({
      picData: {
        a: {
          a1: picData.a.a1 + 1,
        },
        b: {
          b1: 0,
        },
      },
    });
  };

  render() {
    const { picData } = this.state;
    return (
      <div className={styles.parent}>
        <Button onClick={this.changeState}>picData.a.a1</Button>
        <Button onClick={this.changeState2}>picData.a.a1</Button>
        <ChildA AData={picData.a}></ChildA>
        <ChildB BData={picData.b}></ChildB>
      </div>
    );
  }
}

export default MultiComs;
